import config from "config/config.js";

export default {
	data() {
		return {
			username: '',
			password: '',
			passwordup: '',
			allUsers: [],
			userIconImg: '/static/images/dog.jpeg'
		}
	},

	onLoad() {
		console.log("regist  onload");
		this.allUsers = getApp().globalData.allUsers;
		this.userIconImg = getApp().globalData.userIconImg;
	},

	onShow() {

	},

	methods: {

		changeIcon(){ 
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'],
			    success: (chooseImageRes) => {
			        const tempFilePaths = chooseImageRes.tempFilePaths;
			        uni.uploadFile({
			            url: config.reUrl + '/FuStar/RequestImgPath',
			            filePath: tempFilePaths[0],
			            name: 'file',
			            success: (uploadFileRes) => {
			                console.log(uploadFileRes.data);
							this.userIconImg = config.reUrl + '/uploadFile' + uploadFileRes.data;
							getApp().globalData.userIconImg = config.reUrl + '/uploadFile' + uploadFileRes.data;
						}
			        });
			    }
			});
		},

		loginrequest() {
			var nameRE = /^[\u4e00-\u9fa5_A-Za-z0-9]{3,8}$/;
			var pwdRE = /^[0-9]{6,8}$/;
			var nameFlag = nameRE.test(this.username);
			var pwdFlag = pwdRE.test(this.password);
			var norepeat = true;
			for (var i = 0; i < this.allUsers.length; i++) {
				if (this.username == this.allUsers[i].uname) {
					norepeat = false;
					break;
				}
			}
			if (this.username == '') {
				uni.showModal({
					title: '提示',
					content: '请输入用户名',
					showCancel: false,
					confirmText: "确定"
				});
			} else if (!nameFlag) {
				uni.showModal({
					title: '提示',
					content: '用户名不符合规范',
					showCancel: false,
					confirmText: "确定"
				});
			} else if (!norepeat) {
				uni.showModal({
					title: '提示',
					content: '用户名重复，请重新输入',
					showCancel: false,
					confirmText: "确定"
				});
			} else if (this.password == '' || this.passwordup == '') {
				uni.showModal({
					title: '提示',
					content: '请输入密码',
					showCancel: false,
					confirmText: "确定"
				});
			} else if (this.password !== this.passwordup) {
				uni.showModal({
					title: '提示',
					content: '两次输入的密码不一致',
					showCancel: false,
					confirmText: "确定"
				});
			} else if (!pwdFlag) {
				uni.showModal({
					title: '提示',
					content: '密码不符合规范',
					showCancel: false,
					confirmText: "确定"
				});
			} else {
				console.log("success");
				uni.request({
					url: config.reUrl + '/FuStar/AddNewUser',
					data: {
						uname: this.username,
						upwd: this.password,
						uicon: getApp().globalData.userIconImg
					},
					success: (res) => {
						console.log("AddNewUser:", res.data);
						// console.log("typeof :" , typeof res.data);
						if (res.data) {
							uni.redirectTo({
								url: 'index'
							});
							uni.showToast({
								title: '注册成功'
							});
						} else {
							uni.showModal({
								title: '提示',
								content: '注册失败，请重新注册',
								showCancel: false,
								confirmText: "确定"
							});
						}
					}
				});
			}

		}
	}
}
